export const base = addSourceDecorator(() =>
  Vue.extend({
    data() {
      return {
        value: [],
      };
    },
    render() {
      return (
        <div>
          <div>try drag or click</div>
          <div>selected: {this.value.join(",")}</div>
          <DragSelect vModel={this.value}>
            {Array(20)
              .fill(0)
              .map((v, index) => (
                <DragSelectOption value={index}>{index}</DragSelectOption>
              ))}
          </DragSelect>
        </div>
      );
    },
  }), {__STORY__, __ADDS_MAP__,__MAIN_FILE_LOCATION__,__MODULE_DEPENDENCIES__,__LOCAL_DEPENDENCIES__,__SOURCE_PREFIX__,__IDS_TO_FRAMEWORKS__});;

